const scannerView = {
  state: {
    requestSecurity: {
      repository: '192.168.10.118:5000',
      image: 'ubuntu',
      tag: '16.04',
      pagination: {
        from: 0,
        size: 10
      },
      sort: {
        field: 'created_at',
        order: 'asc'
      },
      search: []
    },
    cveList: [],
    paginationTotal: 0
  },
  mutations: {
    setPaginationFrom: (state, from) => {
      state.requestSecurity.pagination.from = from
    },
    setRepository: (state, repository) => {
      state.requestSecurity.repository = repository
    },
    setImage: (state, image) => {
      state.requestSecurity.image = image
    },
    setTag: (state, tag) => {
      state.requestSecurity.tag = tag
    },
    setPaginationSize: (state, size) => {
      state.requestSecurity.pagination.size = size
    },
    setMustValue: (state, value) => {
      state.requestSecurity.must[0].value = value
    },
    setCveList: (state, cveList) => {
      state.cveList = cveList
    },
    setSearch: (state, search) => {
      state.requestSecurity.search = search
    },
    setPaginationTotal: (state, paginationTotal) => {
      state.paginationTotal = paginationTotal
    },
    setSortField: (state, field) => {
      state.requestSecurity.sort.field = field
    },
    setSortOrder: (state, order) => {
      state.requestSecurity.sort.order = order
    }
  }
}
export default scannerView



