const mainView = {
  state: {
    requestImage: {
      repository: '192',
      pagination: {
        from: 0,
        size: 10
      },
      sort: {
        field: 'created_at',
        order: 'asc'
      },
      search: []
    },
    imageList: [],
    paginationTotal: 0
  },
  mutations: {
    setPaginationFrom: (state, from) => {
      state.requestImage.pagination.from = from
    },
    setRepository: (state, from) => {
      state.requestImage.repository = from
    },
    setPaginationSize: (state, size) => {
      state.requestImage.pagination.size = size
    },
    setMustValue: (state, value) => {
      state.requestImage.must[0].value = value
    },
    setImageList: (state, imageList) => {
      state.imageList = imageList
    },
    setPaginationTotal: (state, paginationTotal) => {
      state.paginationTotal = paginationTotal
    },
    setSortField: (state, field) => {
      state.requestImage.sort.field = field
    },
    setSortOrder: (state, order) => {
      state.requestImage.sort.order = order
    }
  }
}
export default mainView



