const getters = {
  requestImage: state => state.mainView.requestImage,
  imageList: state => state.mainView.imageList,
  paginationTotalMain: state => state.mainView.paginationTotal,
  requestSecurity: state => state.scannerView.requestSecurity,
  cveList: state => state.scannerView.cveList,
  paginationTotalScanner: state => state.scannerView.paginationTotal
}
export default getters



