import Vue from 'vue'
import Vuex from 'vuex'
import mainView from './modules/mainView'
import scannerView from './modules/scannerView'
import getters from './getters'

Vue.use(Vuex)

const store = new Vuex.Store({
  modules: {
    mainView,
    scannerView
  },
  getters
})

export default store



