import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export const constantRouterMap = [
  {
    path: '/',
    name: 'main',
    component: () => import('@/views/main/index')
  },
  {
    path: '/scannerView',
    name: 'scannerView',
    redirect: '/scannerView/index',
    component: () => import('@/views/scannerView/index'),
    children: [
      {
        path: 'index',
        component: () => import('@/views/scannerView/overview/index'),
        name: 'index'
      },
      {
        path: 'strategy',
        component: () => import('@/views/scannerView/strategy/index'),
        name: 'strategy'
      },
      {
        path: 'content',
        component: () => import('@/views/scannerView/content/index'),
        name: 'content'
      },
      {
        path: 'security',
        component: () => import('@/views/scannerView/security/index'),
        name: 'security'
      }
    ]
  },
  {
    path: '/monitorView',
    component: () => import('@/views/monitorView/index'),
    name: 'monitorView'
  },
  {
    path: '/shieldView',
    component: () => import('@/views/shieldView/index'),
    name: 'shieldView'
  }
]

export default new Router({
  mode: 'history',
  routes: constantRouterMap
})



